@media (max-width: 1440px) {

    header{
 
        font-size: 3.5rem;
    }


    nav button {
        padding: 5px 15px; 
        font-size: 1rem;
  
    
    }


    #category-header {

        font-size: 1.5em;
    }

    #articles-container {
        column-count: 3;
        column-gap: 20px;
        padding: 20px;
    }

    .article {

        padding: 20px;
    }



    .article h2 {
        font-size: 1.2em;
        margin: 15px;
  
    }
    
    .article p {

        font-size: .8em;
        color: black;

    }
 
    .article a {
        font-size: .8rem;
    }
    
    footer p {
        font-size: 60%; 
    }

    #back-to-top {

        height: 8%;
    
    }
    

    .loading-spinner {
        width: 30px;
        height: 30px;

    }
 

}